/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 */

.custom-block-card-item a {
	text-decoration: none;
	line-height: 0;
}

.custom-block-card {
	display: flex;
	margin-top: 48px;
	flex-direction: column;
}

.custom-block-card-container {
	padding: 0;
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(min(20%, max(383px, 20%)), 1fr));
	justify-items: center;
	row-gap: 32px;
	column-gap: 32px;
}

.custom-block-card-item {
	width: 100%;
	max-width: max(450px, min(240px, 30%));
	overflow: hidden;
	font-family: Heebo, cursive;
	display: flex;
	flex-direction: column;
}

.custom-block-card-item .custom-block-card-image {
	width: 100%;
	height: 280px;
	object-fit: cover;
}

.custom-block-card-item .custom-block-card-detail {
	padding: 24px 16px;
	background-color: var(--secondary-color);
	display: flex;
	flex-direction: column;
	line-height: normal;
	font-size: 16px;
	color: var(--light-spe-color);
	font-weight: 400;
	gap: 5px;
}

.custom-block-card-item .custom-block-card-title {
	font-weight: 700;
	font-size: 22px;
	line-height: normal;
	color: var(--white-spe-color);
}

.custom-block-card-info span {
	color: var(--white-spe-color);
	font-weight: 500;
}

@media screen and (max-width: 1200px) {

	.custom-block-card-container {
		padding: 0;
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(min(100%, max(383px, 20%)), 1fr));
		justify-items: center;
		row-gap: 32px;
		column-gap: 32px;
	}
}

/** Media query for the mobile view */
@media screen and (max-width: 768px) {

	.custom-block-container {
		padding: 0;
	}

	.custom-block-card-item {
		max-width: unset;
		width: 100%;
		overflow: hidden;
		font-size: 8px;
		line-height: 12px;
		margin-top: 0;
		padding-top: 0;
	}

	.custom-block-card-item .custom-block-card-image {
		height: 230px;
	}

	.custom-block-card-item .custom-block-card-detail {
		padding: 15px;
	}
}
